@charset "UTF-8";
:root {
  --main-color: #52668b;
  --additional-color: #89a5da;
  --additional-color-2: #a7be7b;
  --light-blue-background: #eff2f8;
  --white: #fff;
  --text-color: #2e3036;
  --text-800: rgba(46, 48, 54, 0.8);
  --white-text-800: rgba(255, 255, 255, 0.8);
  --blue-line: rgba(82, 102, 139, 0.3);
  --white-line: rgba(255, 255, 255, 0.3);
  --font-family: "Cygre", sans-serif;
}

@font-face {
  font-family: "Cygre";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/Cygre-Regular.woff2") format("woff2"), url("../fonts/Cygre-Regular.woff") format("woff"), local("Roboto");
}
@font-face {
  font-family: "Cygre";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/Cygre-Medium.woff2") format("woff2"), url("../fonts/Cygre-Medium.woff") format("woff"), local("Roboto");
}
@font-face {
  font-family: "Cygre";
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: url("../fonts/Cygre-SemiBold.woff2") format("woff2"), url("../fonts/Cygre-SemiBold.woff") format("woff"), local("Roboto");
}
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a,
a:link,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
}

ul,
ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button,
input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --container-width: 1760px;
  --container-width-section: 1920px;
  --container-padding: 20px;
}

/*  glob */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background: var(--white);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

* {
  font-style: normal;
}

body {
  margin: 0;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0;
  background: var(--white);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.2;
  color: white;
  font-weight: 500;
  font-style: normal;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  width: 100%;
}

.title-h1 {
  font-weight: 600;
  line-height: 110%;
  color: var(--white);
}

.title-h1 {
  font-size: 60px;
}

.title-h2 {
  font-weight: 500;
  font-size: 24px;
  line-height: 130%;
  color: var(--text-color);
}

@media (max-width: 1200px) {
  .title-h1 {
    font-size: 45px;
  }
}
@media (max-width: 768px) {
  .title-h1 {
    font-size: 28px;
  }
}
.logo {
  display: flex;
  align-items: center;
  gap: 14px;
}

.logo__picture {
  display: block;
}

.logo__picture img {
  width: 50px;
  height: 50px;
  min-width: 50px;
  min-height: 50px;
  -o-object-fit: contain;
  object-fit: contain;
}

.logo__name {
  font-weight: 500;
  font-size: 32px;
  color: var(--text-color);
}

.popup-search__body {
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  max-width: var(--container-width);
  width: 100%;
  margin: 0 auto;
}

.popup-search__content {
  padding: 20px;
  border: 1px solid #dfede0;
  border-radius: 20px;
  background: #fff;
  max-width: 100%;
  height: -moz-max-content;
  height: max-content;
  max-height: 100vh;
}

.popup-search__top {
  display: flex;
  gap: 10px;
}

.popup-search__close {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  width: 63px;
  aspect-ratio: 1/1;
  background: #f5f8f5;
  flex-shrink: 0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.popup-search__close svg g {
  transition: all 0.3s ease;
}

.popup-search__close:hover {
  background: #e1f0e1;
}

.popup-search__close:hover svg g {
  opacity: 1;
}

.popup-search__block {
  padding: 0 24px;
  margin: 84px 0 24px;
}

.popup-search__text {
  font-weight: 600;
  font-size: 20px;
  color: #004728;
  margin-bottom: 30px;
}

.popup-search__list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  max-height: 323px;
  overflow-y: auto;
}

.popup-search__list::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

.popup-search__list::-webkit-scrollbar-thumb {
  width: 3px;
  border-radius: 20px;
  background: var(--main-color);
}

.popup-search__list li a {
  display: block;
  border: 1px solid var(--blue-line);
  border-radius: 40px;
  padding: 11px 18px 11px 38px;
  transition: all 0.3s ease;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--text-color);
  position: relative;
}

.popup-search__list li a::after {
  width: 12px;
  height: 12px;
  position: absolute;
  content: "";
  background-image: url(../img/icons/icon.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  left: 14px;
  top: 14px;
  transition: background-image 0.3s ease-in-out;
}

.popup-search__list li a:hover {
  border: 1px solid var(--main-color);
  color: var(--main-color);
}

.popup-search__block-result {
  padding: 0 24px;
  margin: 30px 0 24px;
}

.popup-search__list-result {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-height: 430px;
  overflow-y: auto;
}

.popup-search__list-result::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

.popup-search__list-result::-webkit-scrollbar-thumb {
  width: 3px;
  border-radius: 20px;
  background: var(--main-color);
}

.popup-search__list-result li a {
  display: block;
  font-weight: 400;
  font-size: 16px;
  color: var(--text-color);
}

.popup-search__list-result li a:hover {
  color: var(--main-color);
}

@media (max-width: 1024px) {
  .popup-search__content {
    padding: 40px 16px;
    border-radius: 16px;
  }
  .popup-search__top {
    display: flex;
    gap: 10px;
  }
  .popup-search__close {
    position: absolute;
    top: 14px;
    right: 14px;
    background: transparent;
    width: 14px;
    height: 14px;
  }
  .popup-search__block {
    padding: 0;
    margin: 20px 0;
  }
  .popup-search__text {
    font-size: 16px;
    margin-bottom: 16px;
  }
  .popup-search__list {
    gap: 10px;
    max-height: 320px;
  }
  .popup-search__block-result {
    padding: 0;
    margin: 20px 0 0;
  }
  .popup-search__list-result {
    gap: 10px;
    max-height: 380px;
  }
}
/* popups */
.popup {
  height: 101vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.7);
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 10px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  position: relative;
  width: 100%;
  position: relative;
  transition: transform 0.3s linear;
  transform: rotateY(90deg);
  transform: translateY(30%);
  max-width: 950px;
  border-radius: 30px;
}

.popup.open .popup__content {
  transform: rotateY(0);
  transform: translateY(0);
}

.popup input {
  width: 100%;
}

.close-popup {
  cursor: pointer;
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 1000;
  width: 16px;
  height: 16px;
}

.close-popup svg g {
  transition: all 0.3s ease;
}

.close-popup img {
  height: 100%;
}

.close-popup:hover svg g {
  opacity: 1;
}

@media (max-width: 768px) {
  .popup__content {
    border-radius: 16px;
  }
  .close-popup {
    top: 16px;
    right: 16px;
    width: 14px;
    height: 14px;
  }
}
.none {
  display: none;
}

