.footer {
    margin-bottom: 20px;
}

.footer__top-line::after {
    width: 100%;
    height: 1px;
    position: absolute;
    content: "";
    background-image: url(../img/icons/line2.svg);
    top: 0;
    left: 0;
    right: 0;
    transition: background-image 0.3s ease-in-out;
}

.footer__wrap {
    border-radius: 40px;
    padding: 80px;
    background: var(--text-color);
}

.footer__container {
    max-width: var(--container-width-section);
}

.footer__top {
    padding-bottom: 80px;
    margin-bottom: 87px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.footer__top-text {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: var(--white);
    opacity: 0.8;
}

.footer__top-info {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.footer__logo .logo__name {
    color: var(--white);
}

.footer__logo {
    margin-bottom: 40px;
}

.footer__top-line {
    position: relative;
}

.top-network {
    display: flex;
    gap: 10px;
}

.footer__top-network .network {
    background-color: var(--main-color);
    width: 50px;
    height: 50px;
}

.footer__top-network .network:hover {
    background: white;
}

.top-network__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 130%;
    margin-bottom: 30px;
    color: var(--white);
}

.footer__top-network .network:hover svg path {
    fill: var(--main-color);
}

.footer__search .form-search {
    border: 1px solid var(--white-line);
}

.footer__search .form-search input {
    color: var(--white);
}

.footer__search .form-search input::placeholder {
    color: var(--white);
    opacity: 0.6;
}

.footer__search .popup-search__list-result {
    position: absolute;
    max-height: 430px;
    background: white;
    padding: 20px;
    border-radius: 12px;
}

.footer__special {
    display: flex;
    opacity: 0.8;
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--white);
    gap: 16px;
    align-items: center;
    transition: opacity 0.3s;
}

.footer__special:hover {
    opacity: 1;
}

@media (width < 1200px) {
    .footer__wrap {
        border-radius: 20px 20px 0px 0px;
        padding: 40px 20px;
    }

}

@media (width < 1024px) {
    .footer {
        margin-bottom: 0px;
    }

    .footer__logo {
        margin-bottom: 24px;
    }

    .footer__top-text {
        font-size: 14px;
    }

    .top-network__title {
        font-size: 18px;
        margin-bottom: 20px;
    }

    .footer__top {
        padding-bottom: 40px;
        margin-bottom: 40px;
    }

}

.footer__top-items {
    display: flex;
    justify-content: space-between;
}

.top-references__item-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 130%;
    color: var(--white);
    margin-bottom: 30px;
}

.top-references__item-links {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.top-references__item-link button,
.top-references__item-link a {
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--white);
    opacity: 0.8;
    transition: opacity 0.3s;
    text-align: left;
}

.top-references__item-link:hover button,
.top-references__item-link:hover a {
    opacity: 1;
}

.top-references__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
}

.footer__top-info {
    width: 24.4%;
}

.footer__top-references {
    width: 57.6%;
    display: flex;
    flex-direction: column;
    gap: 60px;
}

@media (width <=1024px) {
    .footer__top-info {
        width: 100%;
    }

    .footer__top-references {
        width: 100%;
        gap: 40px;
    }

    .footer__top-items {
        flex-direction: column;
        gap: 40px;
    }

    .top-references__item-title {
        font-size: 18px;
        margin-bottom: 24px;
    }


    .top-references__item-links {
        gap: 18px;
    }

    .top-references__item-link button,
    .top-references__item-link a {
        font-size: 16px;
    }
}

@media (width <=1024px) {
    .top-references__items {
        gap: 40px;
    }
}

.clinic-rating__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 130%;
    color: var(--white);
    margin-bottom: 30px;
}

.clinic-rating__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    opacity: 0.8;
    color: var(--white);
}

.clinic-rating__img {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
}

@media (width <=1204px) {
    .clinic-rating__title {
        font-size: 18px;
        margin-bottom: 20px;
    }

    .linic-rating__text {
        font-size: 14px;
    }

    .clinic-rating__img {
        gap: 10px;
    }
}

.payment-methods__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 130%;
    color: var(--white);
    margin-bottom: 30px;
}

.payment-methods__elem {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 6px;
    border-radius: 12px;
    width: fit-content;
    padding: 16px 16px 15px 16px;
    opacity: 0.8;
    background: rgba(239, 242, 248, 0.1);
    font-family: var(--font-family);
    font-weight: 400;
    color: var(--white-text-800);
    font-size: 16px;
    max-width: 120px;
    width: 100%;
}

.payment-methods__elems {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

@media (width <=1024px) {
    .payment-methods__title {
        font-size: 18px;
        margin-bottom: 20px;
    }

    .payment-methods__elem {
        font-size: 14px;
        max-width: 100%;
        flex: 0 0 calc((100% - 20px)/3);
        padding: 16px 6px 15px 6px;
    }
}

.footer__bottom__elems {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.footer__bottom__elem p,
.footer__bottom__elem-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--white);
    transition: opacity 0.3s;
    opacity: 0.8;
}

.footer__bottom__elem-link:hover {
    opacity: 1;
}

.footer__bottom__elem p {
    opacity: 0.6;
}

.footer__bottom__elem:first-child {
    width: 24.4%;
}

.footer__bottom__elem:last-child {
    width: 57.6%;
}

.footer__bottom__elem--links {
    display: flex;
    gap: 25px;
    flex-wrap: wrap;
}

@media (width <=1024px) {
    .footer__bottom__elems {
        flex-direction: column-reverse;
        gap: 40px;
    }

    .footer__bottom__elem:first-child,
    .footer__bottom__elem:last-child {
        width: 100%;
    }

    .footer__bottom__elem p,
    .footer__bottom__elem-link {
        font-size: 14px;
    }

    .footer__bottom__elem--links {
        gap: 20px;
        flex-direction: column;
    }

    .footer .container,
    .section .section__container {
        padding: 0;
    }
}

@media (width <=900px) {

    .popup__image {
        justify-content: start;
        margin-bottom: 30px;
        width: 50px;
        height: 50px;
    }
}

@media (width <=500px) {
    .popup-change__btn {
        width: 100%;
    }
}