.popup-search__body {
  display: flex;
  justify-content: flex-end;
  align-items: stretch;
  max-width: var(--container-width);
  width: 100%;
  margin: 0 auto;
}

.popup-search__content {
  padding: 20px;
  border: 1px solid #dfede0;
  border-radius: 20px;
  background: #fff;
  max-width: 100%;
  height: -moz-max-content;
  height: max-content;
  max-height: 100vh;
}

.popup-search__top {
  display: flex;
  gap: 10px;
}

.popup-search__close {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  width: 63px;
  aspect-ratio: 1/1;
  background: #f5f8f5;
  flex-shrink: 0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.popup-search__close svg g {
  transition: all 0.3s ease;
}

.popup-search__close:hover {
  background: #e1f0e1;
}

.popup-search__close:hover svg g {
  opacity: 1;
}

.popup-search__block {
  padding: 0 24px;
  margin: 84px 0 24px;
}

.popup-search__text {
  font-weight: 600;
  font-size: 20px;
  color: #004728;
  margin-bottom: 30px;
}

.popup-search__list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  max-height: 323px;
  overflow-y: auto;
}

.popup-search__list::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

.popup-search__list::-webkit-scrollbar-thumb {
  width: 3px;
  border-radius: 20px;
  background: var(--main-color);
}

.popup-search__list li a {
  display: block;
  border: 1px solid var(--blue-line);
  border-radius: 40px;
  padding: 11px 18px 11px 38px;
  transition: all 0.3s ease;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--text-color);
  position: relative;
}

.popup-search__list li a::after {
  width: 12px;
  height: 12px;
  position: absolute;
  content: "";
  background-image: url(../img/icons/icon.svg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  left: 14px;
  top: 14px;
  transition: background-image 0.3s ease-in-out;
}

.popup-search__list li a:hover {
  border: 1px solid var(--main-color);
  color: var(--main-color);
}

.popup-search__block-result {
  padding: 0 24px;
  margin: 30px 0 24px;
}

.popup-search__list-result {
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-height: 430px;
  overflow-y: auto;
}

.popup-search__list-result::-webkit-scrollbar {
  width: 3px;
  background: transparent;
}

.popup-search__list-result::-webkit-scrollbar-thumb {
  width: 3px;
  border-radius: 20px;
  background: var(--main-color);
}

.popup-search__list-result li a {
  display: block;
  font-weight: 400;
  font-size: 16px;
  color: var(--text-color);
}

.popup-search__list-result li a:hover {
  color: var(--main-color);
}

@media (max-width: 1024px) {
  .popup-search__content {
    padding: 40px 16px;
    border-radius: 16px;
  }

  .popup-search__top {
    display: flex;
    gap: 10px;
  }

  .popup-search__close {
    position: absolute;
    top: 14px;
    right: 14px;
    background: transparent;
    width: 14px;
    height: 14px;
  }

  .popup-search__block {
    padding: 0;
    margin: 20px 0;
  }

  .popup-search__text {
    font-size: 16px;
    margin-bottom: 16px;
  }

  .popup-search__list {
    gap: 10px;
    max-height: 320px;
  }



  .popup-search__block-result {
    padding: 0;
    margin: 20px 0 0;
  }

  .popup-search__list-result {
    gap: 10px;
    max-height: 380px;
  }
}

/* popups */
.popup {
  height: 101vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.7);
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 10px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  position: relative;
  width: 100%;
  position: relative;
  transition: transform 0.3s linear;
  transform: rotateY(90deg);
  transform: translateY(30%);
  max-width: 950px;
  border-radius: 30px;
}

.popup.open .popup__content {
  transform: rotateY(0);
  transform: translateY(0);
}

.popup input {
  width: 100%;
}

.close-popup {
  cursor: pointer;
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 1000;
  width: 16px;
  height: 16px;
}

.close-popup svg g {
  transition: all 0.3s ease;
}

.close-popup img {
  height: 100%;
}

.close-popup:hover svg g {
  opacity: 1;
}

@media (max-width: 768px) {
  .popup__content {
    border-radius: 16px;
  }

  .close-popup {
    top: 16px;
    right: 16px;
    width: 14px;
    height: 14px;
  }
}

