        border-radius: 20px;
    }

    .calculator__result-title {
        font-size: 20px;
    }

    .calculator__result-price {
        font-size: 28px;
    }

    .calculator__result-ctegory,
    .calculator__result-info {
        font-size: 16px;
    }
}

.banner--calculator {
    margin-bottom: 0px;
}

.banner--calculator .section__title-h1 {
    width: 100%;
    margin-bottom: 46px;
}

.calculator__wrap {
    border-radius: 40px;
    padding: 80px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: var(--light-blue-background);
}

.calculator__choice-item {
    border-radius: 16px;
    background: #dae5fa;
    padding: 26px;
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: var(--text-color);
    justify-content: space-between;
    cursor: pointer;
    flex: 0 0 calc((100% - 12px)/2);
}

.calculator__image {
    margin-bottom: 50px;
}

.calculator__choice-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
    text-align: center;
    color: var(--text-color);
    margin-bottom: 32px;
}

.calculator__main {
    border-radius: 30px;
    padding: 50px;
    max-width: 1200px;
    width: 100%;
    background: var(--white);
}

.calculator__choice-items {
    display: flex;
    gap: 12px;
    flex-wrap: wrap;
    margin-bottom: 32px;
    width: 100%;
}

.calculator__choice-btn {
    border-radius: 30px;
    padding: 17px 73px;
    background: var(--main-color);
    font-weight: 500;
    font-size: 18px;
    color: var(--white);
    transition: color 0.3s, background-color 0.3s, opacity 0.3s;
}

.calculator__choice-btn:hover {
    background: var(--additional-color);
    color: white;
}

.calculator__choice {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media (width <=1140px) {
    .calculator__choice-title {
        font-size: 20px;
        margin-bottom: 24px;
    }

    .calculator__main {
        background: var(--light-blue-background);
        padding: 0;
    }

    .calculator__choice-item {
        border-radius: 12px;
        padding: 20px;
        font-size: 16px;
        background: var(--white);
    }

    .calculator__choice-item.active {
        background: #dae5fa;
    }
}

.calculator__image-mob {
    display: none;
}

@media (width <=1024px) {
    .calculator__image {
        display: none;
    }

    .calculator__image-mob {
        margin-bottom: 30px;
        display: flex;
        justify-content: center;
    }

    .banner--calculator .section__title-h1 {
        width: 100%;
        margin-bottom: 24px;
    }

    .calculator__choice-btn {
        padding: 14.5px 73px;
    }

    .calculator__wrap {
        padding: 50px 20px;
    }

    .calculator__choice-items {
        gap: 8px;
        margin-bottom: 24px;
    }
}

@media (width <=800px) {
    .calculator__choice-item {
        flex: 0 0 100%;
    }
}

@media (width <=500px) {
    .calculator__choice-btn {
        max-width: 100%;
        width: 100%;
    }
}

.popup-change__form-block {
    display: flex;
    gap: 12px;
    align-items: center;
}

.popup-change__form-block .custom-checkbox,
.popup-change__form-block button {
    flex: 0 0 calc((100% - 24px)/2);
    max-height: 56px;
    height: 100%;
}

.popup-change__form-block button {
    padding: 18px 27.5px;
}



.popup-city__list-result ul li a,
.popup-city__list ul li a {
    padding: 12px 20px;
    max-height: 50px;
    margin: 5px;
    border: 1px solid var(--blue-line);
    display: flex;
    align-items: center;
    gap: 10px;
    border-radius: 40px;
    position: relative;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: var(--text-color);
    width: fit-content;
}

.popup__image {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
}

.popup-change__btn {
    font-weight: 500;
    font-size: 18px;
    color: var(--white);
    border-radius: 30px;
    background: var(--main-color);
    padding: 16px 30px;
}

.popup-change__btns {
    display: flex;
    justify-content: center;
}

.articles__item.active.hide {
    opacity: 0;
    pointer-events: none;
    transform: scale(0.65);
    max-height: 0;
    margin: 0;
    padding: 0;
}