.header {
    position: relative;
}

.header__top {
    // padding: 20px 0 16px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}

.header__bottom {
    padding-left: 10px;
    padding-right: 10px;
}

.header__top-left {
    display: flex;
    align-items: center;
    gap: 60px;
}

.header__top-right {
    width: 25.11%;
}

.header__items-row {
    display: inline-grid;
    grid-template-columns: repeat(3, minmax(min-content, max-content));
    gap: 60px;
}

.header__item {
    display: flex;
    align-items: center;
    gap: 12px;
    line-height: 1;
    flex: 0 1 auto;
    min-width: -moz-min-content;
    min-width: min-content;
}


.header__item-text {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.header__item-text button {
    display: flex;
    flex-direction: column;
}

.header__item-text span {
    font-weight: 400;
    font-size: 16px;
    color: #6f7c97;
}

.header__item-text p,
.header__item-text a,
.header__item-text button {
    font-weight: 500;
    font-size: 18px;
    color: var(--text-color);
}

.header__item-text a {
    text-wrap: nowrap;
}

.header__item-text a,
.header__item-text button {
    transition: all 0.3s ease;
}

.header__item-text a:hover,
.header__item-text button:hover {
    text-decoration: underline;
}

.header__item-icon {
    flex-shrink: 0;
}

.header__icons {
    display: flex;
    align-items: center;
    gap: 10px;
}

.header__icons .special-btn {
    padding: 0px;
    flex-shrink: 0;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
    border: 1px solid var(--blue-line);
    background-color: white;
    transition: background-color 0.3s, opacity 0.3s;
}

.special-btn:active {
    opacity: 0.8;
}

.special-btn svg path {
    stroke: #52668B;
    transition: stroke 0.3s ease;
}

.special-btn:active svg path {
    stroke: white;
}

.special-btn:hover svg path {
    stroke: white;
}

.header__bottom {
    padding-bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}

.header__nav-list {
    display: flex;
    gap: 40px;
}

.header__nav-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    transition: all 0.3s ease;
}

.header__nav-item svg {
    transition: all 0.3s ease;
}

.header__nav-arrow {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
}

.header__nav-link {
    display: block;
    font-weight: 500;
    font-size: 18px;
    color: var(--text-color);
    transition: color 0.3s ease;
}

.header__nav-sublist {
    padding: 13px 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s ease;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 5;
}

.header__nav-sublist ul {
    padding: 30px 0 10px;
    min-width: 340px;
    border: 1px solid var(--blue-line);
    border-radius: 16px;
    background: var(--white);
}

.header__nav-sublist ul li {
    position: relative;
}

.header__nav-sublist ul a {
    display: block;
    font-weight: 500;
    font-size: 18px;
    color: var(--text-color);
    transition: all 0.3s ease;
    padding: 0px 30px 20px 30px;
}

.header__nav-sublist ul a:hover {
    color: var(--main-color);
}

.header__nav-subsublist {
    padding: 0 8px;
    top: -16px;
    left: 100%;
}

.header__nav-subsublist ul {
    padding: 30px 0 10px;
    min-width: 460px;
}

.header__nav-subsublist ul a {
    color: var(--text-800);
}

.header__btns {
    display: flex;
    gap: 12px;
}

.header-mobile {
    display: none;
}

.header-mobile .network {
    width: 50px;
    height: 50px;

}

.burger {
    border-radius: 40px;
    padding: 16px;
    width: 46px;
    height: 46px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 6px;
    flex-shrink: 0;
    background: var(--additional-color);
}

.burger span {
    display: block;
    width: 16px;
    height: 2px;
    background-color: #ffffff;
    border-radius: 5px;
    transition: all 0.3s ease;
}

.burger.active span:first-child {
    transform: rotate(45deg) translate(3px, 2px);
}

.burger.active span:last-child {
    transform: rotate(-45deg) translate(4px, -2px);
}

@media (width > 1024px) {
    .header__nav-sublist .header__nav-arrow {
        flex-shrink: 0;
        display: flex;
        align-items: center;
        display: none;
        justify-content: center;
        transition: all 0.3s ease;
    }

}

@media (hover: hover) {

    .header__nav-item:hover .header__nav-arrow svg {
        transform: rotate(180deg);
    }

    .header__nav-link:hover {
        color: var(--main-color);
    }

    .header__nav-item:hover {
        color: var(--main-color);
    }

    .header__nav-item:hover>.header__nav-sublist {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    .header__nav-sublist li:hover>.header__nav-subsublist {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
}

@media (max-width: 1360px) {
    .footer .container {
        padding: 0 10px;
    }

    .header__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
    }

    .header__top-left {
        display: flex;
        align-items: center;
        gap: 30px;
    }

    .header__items {
        border: none;
        border-radius: 0;
        padding: 0;
    }

    .header__items-row {
        display: flex;
        gap: 35px;
    }

    .header__item:not(:last-child) {
        padding-right: 0;
        border-right: none;
        margin-right: 0;
    }

    .header__icons {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    .header__icons .special-btn {
        padding: 10px;
    }

    .header__nav-link {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 16px;
        color: var(--text-color);
    }

    .header__nav-sublist ul {
        min-width: 400px;
    }


    .header__nav-subsublist ul {
        min-width: 360px;
    }

    .header__btns {
        gap: 8px;
    }

    .header__btns button {
        padding: 16px 17px;
        font-size: 13px;
    }
}

@media (max-width: 1024px) {
    .header-mobile {
        display: block;
    }

    .main {
        margin-top: 70px;
    }

    .header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 10;
        background-color: #fff;
        // border-bottom: 1px solid var(--blue-line);
    }

    .header__top {
        padding: 12px;
        gap: 10px;
    }

    .header__top-left {
        display: flex;
        align-items: center;
        gap: 30px;
    }

    .header__top-btns {
        display: flex;
        gap: 10px;
        justify-content: end;
    }

    .header__top-btns button:first-child {
        padding: 0;
        width: 46px;
        height: 46px;
        display: flex;
        align-items: center;
        justify-content: center;

    }

    .header__item:not(:last-child) {
        display: none;
    }

    .header__icons {
        display: none;
    }

    .header__bottom {
        position: absolute;
        top: 100%;
        left: -100%;
        width: 100%;
        padding: 30px 10px;
        display: flex;
        align-items: stretch;
        justify-content: flex-start;
        flex-direction: column;
        gap: 30px;
        background: #fff;
        z-index: 10;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s ease;
        height: calc(100vh - 70px);
        overflow-y: auto;
    }

    .header__bottom.active {
        opacity: 1;
        pointer-events: all;
        left: 0;
    }

    .header__bottom::-webkit-scrollbar {
        width: 3px;
        background: transparent;
    }

    .header__bottom::-webkit-scrollbar-thumb {
        width: 3px;
        border-radius: 20px;
        background: var(--main-color);
    }

    .header__search {
        margin-bottom: 12px;
    }

    .header__mobile-btn {
        width: 100%;
    }

    .header-mobile-btn {
        width: 100%;
    }

    .header-mobile .header__icons {
        display: flex;
    }

    .header-mobile .header__items-column {
        display: flex;
        flex-direction: column;
        gap: 24px;
        margin-bottom: 24px;
    }

    .header-mobile .header__item:nth-child(n) {
        display: flex;
    }

    .header__nav-list {
        flex-direction: column;
        gap: 8px;
    }

    .header__nav-sublist .header__nav-ul--dop {
        padding: 0;
    }

    .header__nav-item {
        flex-wrap: wrap;
        padding: 0;
        gap: 0;
        border-radius: 12px;
        background: var(--light-blue-background);
        justify-content: space-between;
    }

    .header__nav-item>.header__nav-arrow {
        display: flex;
    }

    .header__nav-item.active>.header__nav-arrow {
        transform: rotate(180deg);
        width: 12px;
        margin-right: 24px;
    }

    .header__nav-link {
        display: flex;
        justify-content: space-between;
        padding: 20px;
        font-size: 16px;
        width: calc(100% - 60px);


        border-radius: 12px;
        padding: 20px;
        background: var(--light-blue-background);
    }

    .header__nav-arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 40px;
        transition: all 0.3s ease;
    }

    .header__nav-itemService .header__nav-arrow {
        display: flex;
    }

    .header__nav-linkService>svg {
        display: none;
    }

    .header__nav-sublist {
        padding: 0;
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        position: static;
        width: 100%;
        max-height: 0;
        overflow: hidden;
    }

    .header__nav-item.active {
        padding-bottom: 20px;
    }

    .header__nav-item>.header__nav-arrow svg {
        width: 12px;
    }

    .header__nav-item.active>.header__nav-arrow svg {
        width: 12px;
    }

    .header__nav-sublist ul {
        border: none;
        border-radius: 0;
        background: #fff;
        padding: 20px 0;
        min-width: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        background: var(--light-blue-background);
        border-radius: 12px;
        position: relative;
    }


    .header__nav-ul::after {
        width: 100%;
        height: 1px;
        position: absolute;
        content: "";
        background-image: url(../img/icons/line3.svg);
        top: 0;
        left: 0px;
        right: 0;
        transition: background-image 0.3s ease-in-out;
    }

    .header__nav-sublist ul li {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        border-radius: 12px;
        font-size: 16px;
        padding: 0px 24px 0px 20px;
        background: var(--light-blue-background);
    }

    .header__nav-sublist ul li .header__nav-arrow {
        height: 16px;
        width: 12px;
    }

    .header__nav-sublist ul li .header__nav-arrow svg {
        transition: all 0.3s ease;
    }

    .header__nav-sublist ul li .header__nav-arrow.active svg {
        transform: rotate(180deg);
    }

    .header__nav-sublist ul a {
        display: block;
        width: 100%;
        font-weight: 500;
        font-size: 16px;
        padding: 0;
        max-width: calc(100% - 50px);
    }

    .header__nav-subsublist {
        width: 100%;
        padding: 0;
    }

    .header__nav-subsublist ul {
        padding: 0;
        background: transparent;
        min-width: auto;
        gap: 0;
    }

    .header__nav-subsublist ul li {
        padding: 0;
        border-radius: 0;
    }

    .header__nav-subsublist ul a {
        padding: 8px 0;
    }

    .header__nav-sublist ul a.active {
        color: var(--main-color);
    }

    .header__btns {
        display: none;
    }
}

@media (width <=1200px) {
    .header__btns .header__icons {
        display: none;
    }
}

@media (width <=1024px) {
    .header__logo .logo__text {
        font-size: 26px;
    }

    .logo__picture img {
        width: 46px;
        height: 46px;
        min-width: 46px;
        min-height: 46px;
    }

    .header__item-icon img {
        width: 46px;
        height: 46px;
        min-width: 46px;
        min-height: 46px;
    }

    .header__item-text p,
    .header__item-text a,
    .header__item-text button {
        font-size: 16px;
    }

    .header__item-text span {
        font-size: 14px;
    }

    .header__item-text {
        gap: 4px;
        justify-content: center;
    }
}

@media (width <=768px) {
    .header__logo .logo__text {
        display: none;
    }
}

.form-search {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid var(--blue-line);
    border-radius: 30px;
    padding-right: 20px;
    max-width: 402px;
    width: 100%;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    color: #6f7c97;
}

.form-search input {
    display: block;
    max-width: 402px;
    width: 100%;
    border: 0px solid var(--blue-line);
}

.header-mobile .button {
    width: 100%;
    font-size: 16px;
}

.no-results-message {
    font-weight: 400;
    font-size: 16px;
    color: var(--text-800);
    padding-bottom: 20px;
    position: relative;
}

.no-results-message::after {
    width: 100%;
    height: 1px;
    position: absolute;
    content: "";
    background-image: url(../img/icons/line3.svg);
    left: 0px;
    right: 0;
    bottom: 0px;
    transition: background-image 0.3s ease-in-out;
}

.header__contact {
    display: flex;
    flex-direction: column;
    gap: 18px;
    margin-bottom: 30px;
}

.header__contact-title {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: var(--text-color);
    margin-bottom: 6px;
}

.header__contact a,
.header__contact button {
    font-weight: 400;
    font-size: 16px;
    opacity: 0.8;
    color: var(--text-color);
    text-align: left;
}